import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_SPLIT_CELL = Symbol('TABLE_SPLIT_CELL');

export function splitTableCell(table) {
  return {
    type: TABLE_SPLIT_CELL,
    payload: {
      node: table,
    },
  };
}

interface TableSplitTableCellPayload {
  node: Table;
}

interface TableSplitTableCellAction {
  type: typeof TABLE_SPLIT_CELL;
  payload: TableSplitTableCellPayload;
}

export function isTableSplitTableCellAction(
  action: Action,
): action is TableSplitTableCellAction {
  return action.type === TABLE_SPLIT_CELL;
}
